
@-webkit-keyframes slideYDownIn {
from {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
to {
    opacity: 1;
}
}
@keyframes slideYDownIn {
from {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
to {
    opacity: 1;
}
}
.slideYDownIn {
  -webkit-animation-name: slideYDownIn;
          animation-name: slideYDownIn;
}
@-webkit-keyframes slideYDownOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
}
@keyframes slideYDownOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
}
.slideYDownOut {
  -webkit-animation-name: slideYDownOut;
          animation-name: slideYDownOut;
}



