
@-webkit-keyframes slideXLeftIn {
from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}
to {
    opacity: 1;
}
}
@keyframes slideXLeftIn {
from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}
to {
    opacity: 1;
}
}
.slideXLeftIn {
  -webkit-animation-name: slideXLeftIn;
          animation-name: slideXLeftIn;
}
@-webkit-keyframes slideXLeftOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}
}
@keyframes slideXLeftOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}
}
.slideXLeftOut {
  -webkit-animation-name: slideXLeftOut;
          animation-name: slideXLeftOut;
}



