
@-webkit-keyframes slideXRightIn {
from {
    opacity: 0;
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
}
to {
    opacity: 1;
}
}
@keyframes slideXRightIn {
from {
    opacity: 0;
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
}
to {
    opacity: 1;
}
}
.slideXRightIn {
  -webkit-animation-name: slideXRightIn;
          animation-name: slideXRightIn;
}
@-webkit-keyframes slideXRightOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
}
}
@keyframes slideXRightOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
}
}
.slideXRightOut {
  -webkit-animation-name: slideXRightOut;
          animation-name: slideXRightOut;
}



