
@-webkit-keyframes scaleIn {
from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0)
}
to {
    opacity: 1;
}
}
@keyframes scaleIn {
from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0)
}
to {
    opacity: 1;
}
}
.scaleIn {
  -webkit-animation-name: scaleIn;
          animation-name: scaleIn;
}
@-webkit-keyframes scaleOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes scaleOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.scaleOut {
  -webkit-animation-name: scaleOut;
          animation-name: scaleOut;
}



