
@-webkit-keyframes slideYIn {
from {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
to {
    opacity: 1;
}
}
@keyframes slideYIn {
from {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
to {
    opacity: 1;
}
}
.slideYIn {
  -webkit-animation-name: slideYIn;
          animation-name: slideYIn;
}
@-webkit-keyframes slideYOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
}
@keyframes slideYOut {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
}
.slideYOut {
  -webkit-animation-name: slideYOut;
          animation-name: slideYOut;
}



